<template>
    
    <nav ref="refMainNav" class="main-nav no-print" :class="{'show': isShow}">

        <!-- 選單列 -->
        <div class="nav-bar">
            <button @click="controlNavMenu" class="nav-bar-button"><span class="material-symbols-outlined">menu</span></button>
        </div>

        <!-- 選單內容 -->
        <div class="nav-menu">
            <span class="nav-menu-title">Joy Tools</span>

            <div class="nav-menu-inner">

                <ul class="btn-link-wrap">
                    <li class="btn-link-item">
                        <router-link to="/">
                            <div class="image-wrap" :style="{'background-image': `url(${require('@/assets/images/toolbox.png')})`}"></div>
                            <span>主頁</span>
                        </router-link>
                    </li>
                </ul>

                <ul v-if="false">
                    <li>
                        <router-link to="/test">練習場</router-link>
                        <ul>
                            <li><router-link to="/test/todo-list">待辦事項</router-link></li>
                            <li><router-link to="/test/markdown-editor">markdown編輯器</router-link></li>
                            <li><router-link to="/test/name-component">操作資料練習</router-link></li>
                        </ul>
                    </li>
                </ul>

                <!-- 公開專案 nav -->
                <PublicMenu />


            </div>

            <!-- ko-fi: Buy me a Coffee -->
            <!-- <a href="https://ko-fi.com/joyxie" target="_blank" class="bmc-btn">
                <img src="@/assets/images/bmc/BuyMeACoffee_Stroke@2x.png" alt="Buy me a coffee">
            </a> -->

            <a href="https://portaly.cc/joyxie/support" target="_blank" class="bmc-btn">
                <img src="@/assets/images/bmc/portaly.jpg" alt="Buy me a coffee">
                <span>Buy me a coffee</span>
            </a>
        </div>

    </nav>

</template>
<script>
import { onMounted, nextTick, ref } from 'vue';

import PublicMenu from './PublicMenu.vue';

export default {
    name: 'BaseNavigation',
    components: {
        PublicMenu
    },

    setup() {
        // 數據
        let isShow = ref(false);

        // 元素
        const refMainNav = ref(null);


        function controlNavMenu() {
            if ( isShow.value ) {
                isShow.value = false;
                document.body.classList.remove("modal-open");
            } else {
                isShow.value = true;
                document.body.classList.add("modal-open");
            }
        }


        // 設定裝置高度 vh
        function setDeviceHeight() {
            // First we get the viewport height and we multiple it by 1% to get a value for a vh unit
            let vh = window.innerHeight * 0.01;

            // Then we set the value in the --vh custom property to the root of the document
            document.documentElement.style.setProperty('--vh', `${vh}px`);
        }


        onMounted(()=>{

            nextTick(()=>{
                setDeviceHeight();  // 設定裝置高度 vh

                window.addEventListener('resize', function(){
                    setDeviceHeight();  // 設定裝置高度 vh
                })
            })
        })

        
        

        return {
            isShow,
            refMainNav,

            controlNavMenu,
        }
    }
}
</script>
