import { createRouter, createWebHashHistory } from 'vue-router'


// 目前日期
let today = new Date();
let yy = (today.getFullYear()).toString().substr(2, 2); // 取後兩個字
let mm = (today.getMonth()+1).toString().padStart(2, "0");
let dd = (today.getDate()).toString().padStart(2, "0");

// 設定頁面標題
// document.title = `${yy}${mm}${dd}_Synctify_Design_Invoice`;


// VUE 3 課程筆記 (八) Vue Router
// https://hackmd.io/@vanCS/ByuzU-rcs
const router = createRouter({
    history: createWebHashHistory(),

    // 為 router-link 增加啟用 class (預設為 .router-link-active)
    linkActiveClass: 'active',

    routes: [
        {
            path: '/',
            name: 'index', // 首頁
            meta: { title: 'JOY 小工具' },
            component: () => import('../components/DashBoard.vue'),
        },
        {
            path: '/public',
            name: 'public', // 公開項目
            component: () => import('../components/public/ThePublic.vue'),
            redirect: {
                // 重新導向到的位置, 可以用路徑也可以使用設定好的 name
                // name: 'quote-calculation',
                name: 'not-found'
            },
            children: [
                {
                    path: 'quote-calculation',
                    name: 'quote-calculation', // 勞務報酬計算機
                    meta: { 
                        title: '勞務報酬計算機', 
                        icon: require('../assets/images/calculator.svg')
                    },
                    component: () => import('../components/public/quote-calculation/QuoteCalculation.vue')
                },
                {
                    path: 'time-zone',
                    name: 'time-zone', // 時區對照表
                    meta: { 
                        title: '時區對照表',
                        icon: require('../assets/images/time-zone.svg')
                    },
                    component: () => import('../components/public/time-zone/TimeZone.vue')
                },
                {
                    path: 'working-hours-calculation',
                    name: 'working-hours-calculation', // 工時計算機
                    meta: { 
                        title: '工時計算機',
                        icon: require('../assets/images/working-hours.png')
                    },
                    component: () => import('../components/public/working-hours-calculation/WorkingHoursCalculation.vue')
                },
                {
                    path: 'bill-generator',
                    name: 'bill-generator', // 請款單產生器
                    meta: { 
                        title: `${yy}${mm}${dd}_Synctify_Design_Invoice`,
                        icon: require('../assets/images/bill-generator/favicon.png')
                    },
                    component: () => import('../components/public/bill-generator/BillGenerator.vue')
                },
                {
                    path: 'ratio-aspect',
                    name: 'ratio-aspect', // 寬高求比例計算機
                    meta: { 
                        title: '寬高求比例計算機',
                        icon: require('../assets/images/ratio.png')
                    },
                    component: () => import('../components/public/ratio-aspect/RatioAspect.vue')
                },
                {
                    path: 'ratio-aspect-size',
                    name: 'ratio-aspect-size', // 比例求寬高計算機
                    meta: { 
                        title: '比例求寬高計算機',
                        icon: require('../assets/images/ratio.png')
                    },
                    component: () => import('../components/public/ratio-aspect/RatioAspectSize.vue')
                },
                {
                    path: 'word-dictionary',
                    name: 'word-dictionary', // 國高中英文單字字典
                    meta: { 
                        title: '國高中單字字典',
                        icon: require('../assets/images/dictionary.png')
                    },
                    component: () => import('../components/public/word-dictionary/WordDictionary.vue')
                },
                {
                    path: 'device-size',
                    name: 'device-size', // 檢測裝置尺寸
                    meta: { 
                        title: '檢測裝置尺寸',
                        icon: require('../assets/images/stethoscope.png')
                    },
                    component: () => import('../components/public/device-size/DeviceSize.vue')
                },
                {
                    path: 'sixteen-grider',
                    name: 'sixteen-grider', // 圖片格狀拼版工具
                    meta: { 
                        title: '圖片格狀拼版工具',
                        icon: require('../assets/images/grid.svg')
                    },
                    component: () => import('../components/public/sixteen-grider/SixteenGrider.vue')
                },
                {
                    path: 'serial-pictures-reader',
                    name: 'serial-pictures-reader', // 連續圖片預覽工具
                    meta: { 
                        title: '連續圖片預覽工具',
                        icon: require('../assets/images/comic-book.svg')
                    },
                    component: () => import('../components/public/serial-pictures-reader/SerialPicturesReader.vue')
                },
                
            ]
        },
        {
            path: '/test',
            name: 'test', // 測試項目
            component: () => import('../components/test/TheTest.vue'),
            // redirect: {
            //     // 重新導向到的位置, 可以用路徑也可以使用設定好的 name
            //     name: 'name-component',
            // },
            children: [
                {
                    path: 'todo-list',
                    name: 'todo-list', // 待辦清單
                    component: () => import('../components/test/todolist/TodoList.vue')
                },
                {
                    path: 'markdown-editor',
                    name: 'markdown-editor', // Markdown格式編譯
                    component: () => import('../components/test/MarkdownEditor.vue')
                },
                {
                    path:'name-component',
                    name: 'name-component', // 操作資料練習
                    components: {
                        name1: () => import('../components/test/name/NameComponent1.vue'),
                        name2: () => import('../components/test/name/NameComponent2.vue'),
                        name3: () => import('../components/test/name/NameComponent3.vue')
                    }
                },
                {
                    path: 'pass-data',
                    name: 'pass-data', // 祖孫傳資料練習
                    component: () => import('../components/test/pass-data/TheParent.vue')
                },
            ]
        },
        {
            // 這邊就是用 ＊ 去選擇所有頁面比對有沒有符合的路徑
            // 如果沒有就會導到這個 router-link 來
            path: '/:pathMatch(.*)*',
            name: 'not-found',
            component: () => import('../components/NotFound.vue')
        },




        // {
        //     path: '/about',
        //     name: 'about',
        //     // route level code-splitting
        //     // this generates a separate chunk (about.[hash].js) for this route
        //     // which is lazy-loaded when the route is visited.
        //     // component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
        //     component: () => import('../components/PageAbout.vue')
        // },



        // {
        //     path: '/newpage',
        //     name: '新增頁面',
        //     component: () => import('../views/NewPage.vue')
        // }
    ]
})

router.beforeEach((to, from, next) => {
    // 修改網頁 title
    document.title = to.meta.title || "JOY 小工具";

    // 修改網頁 icon
    let icon_src = to.meta.icon || 'toolbox.png';
    document.querySelector('[rel="icon"]').setAttribute('href', icon_src);

    
    next();
});

export default router